@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

:root {
    --color-1: cyan;
    --color-2: yellow;

    --gradient: linear-gradient(135deg,
        cyan,
        yellow);
}

.container {
    display: flex;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
}

.box {
    height: 80%;
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.counter {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: start;
    padding-top: 80px;
    height: 80%;
    width: 80%;
}

.name {
    font-size: 50px;
        background: linear-gradient(135deg, #2115d3, #ec1fe2);
        -webkit-background-clip: text;
        color: transparent;

}

.btn {
    background: linear-gradient(135deg, #b218e5, #18b1e3);
    height: 120px;
    width: 100px;
    padding: px;
    margin-top: 30px;
    border: none;
    border-radius: 15px;
    color: aliceblue;
    font-size: 80px;
    box-shadow: 20px, 20px, 10px, #18b1e3;
}

.btn:active {
    opacity: 0.5;
}

.txt {
    background: linear-gradient(135deg, #2115d3, #ec1fe2);
    -webkit-background-clip: text;
    color: transparent;
    font-size: 120px;
}